import React, { useState, useEffect} from 'react'

import { useHistory } from 'react-router-dom';

// Assets and Styling
import sling from "./../imgs/sling.png"
import "./../css/content.scss";

const Presales = () => {
    const [opacity, setOpacity] = useState(false);
    const [imageOpacity, setImageOpacity] = useState(false);
    const [headerOpacity, setHeaderOpacity] = useState(false);
    const history = useHistory();

    useEffect(() => {
        setHeaderOpacity(true);
    }, [headerOpacity]);

    useEffect(() => {
        setOpacity(true);
    }, [opacity]);

    useEffect(() => {
        setImageOpacity(true);
    }, [imageOpacity]);

    const orderClickHandler = () => {
        history.push("/order");
    }

    return (
        <>
            <h1 className={headerOpacity ? "headerShow" : "textHide"}> Presales</h1>

            <p className={opacity ? "textShow" : "textHide"}> We've finished up illustrations and just recieved our first batch of books. We're waiting on the next batch and expect to ship out most orders in late June once we recieve them!
            </p>
            <div className='button-order'>
            <button className={opacity ? "textShow" : "textHide"} onClick = {orderClickHandler} > Order now! </button>
            </div>
            <img src = {sling} className={imageOpacity ? "show" : "hide"} alt={"Cover of tinker tim"} />  
        </>
    )
}

export default Presales