import React, { useState, useEffect } from "react";
import "./../css/content.scss";
import cover from "./../imgs/cover.png";
import scold from "./../imgs/dripping_scold.png";
import plane from "./../imgs/plane_brother.png";
import comfort from "./../imgs/comforting_tim.png";
import folding from "./../imgs/folding_instructions.jpg"
import plane_template from "./../imgs/Tinker_Tim_Plane.pdf"

const Plane = () => {
    const [opacity, setOpacity] = useState(false);
    const [imageOpacity, setImageOpacity] = useState(false);
    const [headerOpacity, setHeaderOpacity] = useState(false);
  
    useEffect(() => {
      setHeaderOpacity(true);
    }, [headerOpacity]);
  
    useEffect(() => {
      setOpacity(true);
    }, [opacity]);
  
    useEffect(() => {
      setImageOpacity(true);
    }, [imageOpacity]);
  
    return (
      <>
        <h1 className={headerOpacity ? "headerShow" : "textHide"}> The Plane </h1>
    
        <p className={opacity ? "textShow" : "textHide"}>
            In line with our goal of learning through tinkering, 
            we would like kids to have the ability to make the same paper airplane that Tim made in the story!</p>
        
        <img className={imageOpacity ? "show" : "hide"} src={plane} alt={"Tim flying a paper airplane for his brother"} />
        
        
        <p className={opacity ? "textShow" : "textHide"}>
           <a href={plane_template} target={"_blank"}>Print out this PDF</a>, and print it double-sided along the long edge. Then, follow these instrucions to re-create the plane!</p>

        <img className={imageOpacity ? "show" : "hide"} src={folding} alt={"Tim flying a paper airplane for his brother"} />

        
  
        <div className='line-breaks'></div>
      </>
    )
  }
  
  export default Plane