import React, { useState, useEffect } from 'react'
import family from "./../imgs/family.png";

// Assets and Styling
import "./../css/content.scss";

const Team = () => {
    const [opacity, setOpacity] = useState(false);
    const [imageOpacity, setImageOpacity] = useState(false);
    const [headerOpacity, setHeaderOpacity] = useState(false);

    useEffect(() => {
        setHeaderOpacity(true);
    }, [headerOpacity]);

    useEffect(() => {
        setOpacity(true);
    }, [opacity]);

    useEffect(() => {
        setImageOpacity(true);
    }, [imageOpacity]);

    return (
        <>
            <h1 className={headerOpacity ? "headerShow" : "textHide"}> Illustrator and Authors </h1>
            <h2 className={headerOpacity ? "textShow" : "textHide"}> Illustrated by Shiuan Chu </h2>
            <h2 className={headerOpacity ? "textShow" : "textHide"}> Written by Alex LaFleur, Aroosh Kumar, Darren Huang, and Kenny Ma </h2>

            <p className={opacity ? "textShow" : "textHide"}>
            Shiuan, the illustrator, is an interdisciplinary art student at the University of Washington who has worked on a variety of projects, including animation, ceramics, and other art. She's always wanted to illustrate a children's picture book, and we couldn't be happier with how it turned out.
            <br></br><br></br>
            The authors are a group of four students at the University of Washington. Darren and Alex will be graduating this year, Kenny will be pursuing his Masters, and Aroosh will be entering his senior year. </p>

            <img src={family} className={imageOpacity ? "show" : "hide"} alt={"Cover of tinker tim"} />
        </>
    )
}

export default Team;